@charset "utf-8";

/* Section 1 */
.ginseng-section1 img {
  width: 100%;
}
.ginseng-title {
  text-align: center;
  color: #222;
  padding-top: 11.3333333333%;
}
.ginseng-title h3 {
  font-size: max(var(--font-size), 4vw);
  font-weight: 600;
}
.ginseng-title p {
  font-size: max(var(--font-size), 2.9333333333vw);
  font-weight: 300;
  color: #888;
  margin-top: 3%;
}
.ginseng-title .ginsengName-kr {
  font-size: max(var(--font-size), 6.9333333333vw);
  font-weight: 700;
  margin-top: 4.9333333333%;
}
.ginseng-title .ginsengName-en {
  font-size: max(var(--font-size), 4vw);
  color: #444;
  margin-top: 1.8666666667%;
}
.ginseng-movie {
  margin-top: 8.4%;
}
.ginseng-movie-mo {
  display: block;
}
.ginseng-movie-pc {
  display: none;
}
video {
  width: 100%;
}
/* 1920 */
@media screen and (min-width: 751px) {
  .section1-title-wrap {
    margin: 0 auto;
    max-width: 1440px;
    padding: 0 12.5%;
  }
  .ginseng-title {
    padding-top: 6.944444444444445%;
  }
  .ginseng-title h3 {
    font-size: max(var(--font-size), 1.4583333333333333vw);
  }
  .ginseng-title p {
    font-size: max(var(--font-size), 0.9375vw);
    margin-top: 0.9027777777777777%;
  }
  .ginseng-title .ginsengName-kr {
    font-size: max(var(--font-size), 2.5vw);
    /*  */
    font-weight: 800;
    margin-top: 2.2916666666666665%;
  }
  .ginseng-title .ginsengName-en {
    font-size: max(var(--font-size), 1.4583333333333333vw);
    margin-top: 0.9027777777777777%;
  }
  .ginseng-movie {
    margin-top: 2.96875%;
  }
  /*   */
  .ginseng-movie-mo {
    display: none;
  }
  /*   */
  .ginseng-movie-pc {
    display: block;
  }
}
